.faq {
  position: relative;
  &__container {
    position: relative;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 130px;
      z-index: 1;
      border: 1px solid #ECE1D6;
      width: 557px;
      height: 887px;
    }
  }

  &__headline {
    margin-bottom: 70px;
  }

  &__row {
    margin-top: 50px;
    gap: 70px;
  }

  &__body {
    width: 682px;
    max-width: 100%;
  }

  &__headline {
    font-family: var(--font-audrey);
    font-size: clamp(1.375rem, 0.964rem + 2.05vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    text-transform: uppercase;
    color: var(--black-color);
  }

  &__images {
    padding-top: 60px;
  }

  &__image {
    &--lg {
      position: absolute;
      right: -18%;
      z-index: 2;
      margin-bottom: 30px;
      max-width: 558px;
      height: 594px;
    }

    &--sm {
      position: absolute;
      right: -18%;
      bottom: 300px;
      z-index: 2;
      max-width: 485px;
      height: 270px;
    }
  }

}

.ac {
  margin-top: 0;
  border: none;
  background-color: transparent;
}

.accordion {

  &__list {
    max-width: 682px;
  }

  &__item {
    border-bottom: 1px solid #E2E2E2;
    padding: 30px 0 15px;

  }

  &__top {
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    margin-bottom: 15px;
  }

  &__bottom {
    max-width: 600px;
  }


  &__text {
    font-size: 18px;
    line-height: 144%;
    color: var(--light-gray);
  }


  &__subtitle {
    max-width: 600px;
    font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
    font-weight: var(--regular-font);
    line-height: 128%;
  }

  &__btn {
    cursor: pointer;
    position: relative;
    display: block;
    width: 25px;
    height: 25px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 25px;
      height: 1px;
      background-color: var(--grey-color);
      transform: translate(-50%, -50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 1px;
      height: 25px;
      background-color: var(--grey-color);
      transform: translate(-50%, -50%);
      transition: .2s ease-in-out;
    }
  }
}

.accordion__item.is-active .accordion__btn::after {
  opacity: 0;
}

@media screen and (max-width:1150px) {
  .faq {
    &__container {
      &::after {
        display: none;
      }
    }
    &__images {
      display: none;
    }
    &__body {
      width: 100%;
      max-width: 100%;
    }
  }
  .accordion {
    &__list {
      max-width: 100%;
    }
    &__subtitle {
      max-width: 90vw;
    }
  }
}

@media screen and (max-width:1024px) {
  .faq {
    &__container {
      &::before {
        background-size: contain;
      }
    }
  }
}

@media screen and (max-width:500px) {
  .faq {
    &__headline {
      margin-bottom: 30px;
    }
  }
}
