html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page {
  overflow-x: hidden;
  height: 100%;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  overflow: hidden;
  width: 100%;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}

.primary__btn {
  position: relative;
  display: block;
  span {
    position: relative;
    z-index: 2;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    width: 350px;
    padding: 23px 25px 17px;
    font-family: var(--font-audrey);
    font-size: 18px;
    font-weight: var(--medium-font);
    line-height: 133%;
    text-transform: uppercase;
    color: var(--white-color);
    background-color: var(--green-color);
    transform: translate(20px, 20px);
    transition: .2s ease-in;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    border: 1px solid var(--green-color);
    border-radius: 3px;
    width: 350px;
    height: 70px;
    transition: .2s ease-in;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    span {
      transform: translate(0, 0);
    }

    &::before {
      opacity: 0;
    }
  }

  &:active {
    span {
      color: var(--beige-color);
    }
  }
}

.section {
  padding: 100px 0;

  &__title {
    margin-bottom: 60px;
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    font-weight: var(--regular-font);
    line-height: 128%;
    color: var(--grey-color);
    text-transform: capitalize;
  }

  &__headline {
    margin-bottom: 60px;
    font-family: var(--font-audrey);
    font-size: clamp(1.563rem, 1.205rem + 1.79vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    text-transform: uppercase;
  }
}

.primary__btn.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}

@media screen and (max-width:768px) {
  .section {
    padding: 50px 0;
    &__title {
      margin-bottom: 30px;
    }
  }
}

@media screen and (max-width:425px) {
  .primary__btn {
    span {
      width: 80vw;
    }
    &::before {
      width: 80vw;
    }
  }
}

@media screen and (max-width:375px) {
  .primary__btn {
    span {
      width: 85vw;
    }
    &::before {
      width: 85vw;
    }
  }
}
