.price {
  position: relative;
  background-color: var(--beige-color);
  background-image: url("../img/price_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__container {
    position: relative;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__headline {
    text-align: center;
    color: var(--black-color);
  }

  &__title {
    margin-top: 35px;
  }
}

.tabs {
  overflow-x: auto;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 60px;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    margin: 0 auto;
    max-width: 1050px;
  }
}

.tab {
  cursor: pointer;
  position: relative;
  padding-bottom: 10px;
  overflow: hidden;
  font-family: var(--font-audrey);
  font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
  font-weight: var(--medium-font);
  line-height: 120%;
  text-transform: uppercase;
  text-align: center;
  color: var(--green-color);
  transition: 0.2s ease-in;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: var(--green-color);
    transform: translateX(-100%);
    transition: .3s ease-in;
  }

  &__btn {

    span {
      color: var(--green-color);
      background-color: var(--white-color);
    }

    &::before {
      top: 0;
      border-color: var(--white-color);
    }
  }

  &.active {
    &::before {
      transform: translateX(0);
    }
  }
}

.tab-content {
  position: relative;
  z-index: 2;
  overflow: hidden;
  display: none;
  padding: 70px;
  background-color: var(--green-color);
  transition: opacity 0.3s ease;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 291px;
    height: 353px;
    background-image: url("../img/decor3.png");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &::after {
    content: "";
    position: absolute;
    right: 0;
    z-index: -1;
    bottom: -205px;
    width: 399px;
    height: 485px;
    background-image: url("../img/decor2.png");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &[data-tab].active {
    display: block;
    opacity: 1;
  }
}

.button__container {
  display: grid;
  place-items: center;
}

.chars {
  margin: 0 0 70px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.chars__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 45px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 30px;
    background-image: url("../img/price-star.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &:not(:last-child) {
    margin-bottom: 30px;
  }
}

.chars__term {
  flex: 0 1 auto;
  margin-right: 10px;

  &-title {
    margin-bottom: 5px;
    font-family: var(--font-audrey);
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    font-weight: var(--medium-font);
    line-height: 133%;
    color: #ECE1D6;
    text-transform: uppercase;
  }

  &-time {
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: var(--white-color);
  }
}

.chars__dots {
  flex: 1;
  overflow: hidden;
  white-space: nowrap;
  height: 1em;
  color: var(--beige-color);
}

.chars__dots::before {
  content: "..................................................................................................................................................................................................................................................";
  display: inline-block;
  width: 100%;
}

.chars__val {
  display: flex;
  width: 83px;
  padding: 5px;
  justify-content: center;
  align-items: center;
  flex: 0 1 auto;
  margin-left: 10px;
  white-space: nowrap;
  border-radius: 3px;
  font-size: 25px;
  font-weight: var(--regular-font);
  line-height: 128%;
  color: var(--white-color);
  text-decoration: none;
  background: linear-gradient(90deg, #C48D60 22.27%, #BB875E 77.98%);
}

.tab__headline {
  margin-bottom: 50px;
  font-family: var(--font-audrey);
  font-size: 25px;
  font-weight: var(--medium-font);
  line-height: 120%;
  color: var(--white-color);
  text-align: center;
  text-transform: uppercase;

  &--flex {
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
}

.chars__price {
  align-items: center;
  margin-left: 10px;

  &--old {
    font-size: 25px;
    line-height: 128%;
    color: var(--white-color);
    text-decoration: line-through;
  }
}

@media screen and (max-width:1024px) {
  .price {
    &__container {
      &::before {
        background-size: contain;
      }
    }
  }
  .tab {
    min-width: 310px;
  }
}

@media screen and (max-width:767px) {
  .tabs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  .tab-content {
    padding: 50px 30px;
  }
}

@media screen and (max-width:500px) {
  .tabs {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .tab-content {
    padding: 50px 10px;
  }

  .price {
    &__headline {
      margin-bottom: 30px;
    }

    &__title {
      margin: 15px 0 50px;
    }

    &__container {
      padding: 15px 0 0;
    }
  }

  .tab__btn {
    span {
      width: 70vw;
    }

    &::before {
      width: 100%;
    }
  }

  .tab__headline--flex {
    img {
      display: none;
    }
  }
}
