:root {
  // base
  --font-family: "Source Sans Pro", sans-serif;
  --font-audrey: "Audrey", sans-serif;

  --regular-font: 400;
  --medium-font: 500;
  --semibold-font: 600;

  --content-width: 1300px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --green-color: #5C7572;
  --white-color: #ffffff;
  --black-color: #2B2A29;
  --beige-color: #DED1C7;
  --grey-color: #3B3A3A;
  --light-gray: #787D7C;
}
