.footer {
  padding: 50px 0 40px;
  background-color: var(--green-color);

  &__logo {
    width: 140px;
    height: 105px;
  }

  &__container {
    align-items: center;
    justify-content: space-between;
    gap: 15px;
  }

  &__nav {
    align-items: center;
    gap: 80px;
  }

  &__list {
    gap: 30px;

    &-link {
      font-family: var(--font-audrey);
      font-size: 18px;
      font-weight: var(--medium-font);
      line-height: 133%;
      text-transform: uppercase;
      color: var(--white-color);
      transition: all .2s ease-in;

      &:focus {
        outline: none;
      }

      @include hover {
        color: #BB875E;
      }

      &:active {
        color: #C99B79;
      }
    }
  }

  &__col {
    &--left {
      align-items: center;
      gap: 50px;
    }
  }

  &__btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-bottom: 25px;
    border: 1px solid var(--white-color);
    border-radius: 3px;
    padding: 20px 25px 15px;
    transition: .2s ease-in;

    span {
      position: relative;
      z-index: 2;
      font-family: var(--font-audrey);
      font-weight: var(--medium-font);
      line-height: 125%;
      text-transform: uppercase;
      color: var(--white-color);
      transition: .2s ease-in;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: var(--white-color);
      transform: translateY(-100%);
      transition: .2s ease-in;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--green-color);
      }

      &::before {
        transform: translateY(0);
      }
    }

    &:active {
      span {
        color: var(--beige-color);
      }
    }
  }

  &__copyright {
    font-size: 12px;
    line-height: 133%;
    text-align: right;
    color: #E2E2E2;
  }
}

.social {
  &__list {
    gap: 30px;
  }

  &__icon {
    width: 30px;
    height: 30px;
    fill: var(--white-color);
    transition: fill .2s ease-in;

    &:focus {
      outline: none;
    }

    @include hover {
      fill: #BB875E;
    }

    &:active {
      fill: #C99B79;
    }
  }
}

@media screen and (max-width:1024px) {
  .footer {
    padding: 20px 0 10px;
    &__container {
      flex-direction: column;
    }
    &__col {
      &--left {
        flex-direction: column;
      }
    }
  }
}


@media screen and (max-width:767px) {
  .footer {
    padding: 20px 0 10px;
      &__nav {
        flex-direction: column;
        gap: 15px;
      }
  }
}

@media screen and (max-width:425px) {
  .footer {
    &__list {
      flex-direction: column;
    }
  }
}
