.reviews {
  position: relative;
  background-image: url("../img/reviews_bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;

  &__container {
    position: relative;
    z-index: 2;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__title {
    margin-top: 35px;
  }

  &__headline {
    margin: 0 auto 50px;
    max-width: 995px;
    font-family: var(--font-audrey);
    font-size: clamp(1.875rem, 1.339rem + 2.68vw, 3.75rem);
    font-weight: var(--regular-font);
    line-height: 150%;
    text-transform: uppercase;
    text-align: center;
    color: var(--black-color);
  }

  &__btn {
    &::before {
      top: 0;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    width: 100%;
    height: 170px;
    background: linear-gradient(0deg, #CFCCC8 0%, rgba(207, 204, 200, 0.96) 3.61%, rgba(207, 204, 200, 0.00) 100%);
    transform: rotate(180deg);
  }

}

@media screen and (max-width:1024px) {
  .reviews {
    &__container {
      &::before {
        background-size: contain;
      }
    }
  }
}

