.about {
  position: relative;
  background: linear-gradient(180deg, #ECE1D6 10.36%, #FFF 100%);

  &__container {
    position: relative;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }


  &__row {
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;
    margin-top: 50px;
  }

  &__col {
    flex-direction: column;

    &--sm {
      justify-content: space-between;
      max-width: 333px;
    }

    &:nth-child(3) {
      flex-direction: column-reverse;

      .about__image {
        margin-bottom: 0;
        margin-top: -10px;
      }
    }
  }

  &__image {
    margin-bottom: 10px;
    height: 260px;
    object-fit: cover;

    &--lg {
      height: 603px;
      object-fit: cover;
    }
  }

  &__text {
    margin-bottom: 20px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    color: #787D7C;

    &--accent {
      margin-bottom: 43px;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      line-height: 144%;
      color: var(--green-color);
      text-align: center;
    }
  }

  &__subtitle {
    margin-bottom: 15px;
    font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
    font-weight: var(--semibold-font);
    line-height: 128%;
    text-align: center;
    color: var(--black-color);
  }

  &__col-title {
    font-family: var(--font-audrey);
    font-size: clamp(1.563rem, 1.205rem + 1.79vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    text-transform: uppercase;
    text-align: center;
    background: linear-gradient(90deg, #C48D60 22.27%, #BB875E 77.98%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

@media screen and (max-width:1024px) {
  .about {
    &__container {
      &::before {
        background-size: contain;
      }
    }
    &__row {
      flex-direction: column;
    }
    &__col {
      &--sm {
        max-width: 100%;
      }
      &--lg {
        margin: auto;
      }
      &:nth-child(1) {
        order: 2;
      }
      &:nth-child(2) {
        order: 1;
      }
      &:nth-child(3) {
        order: 3;
      }
    }
    &__image {
      height: 100%;
    }
  }
}


@media screen and (max-width:767px) {
  .about {
    &__image {
      height: 100%;
    }
  }
}

@media screen  and (max-width:500px) {
  .about {
    &__row {
      gap: 10px;
      margin-top: 30px;
    }
    &__image {
      &--lg {
        margin-bottom: 15px;
        height: 100%;
      }
    }
  }
}
