.hero {
  overflow: hidden;
  margin-top: 100px;
  padding: 220px 0 126px;
  min-height: 803px;
  background-color: #ECE1D6;

  &__info {
    position: relative;
    z-index: 3;
    max-width: 820px;
  }

  &__headline {
    margin-bottom: 15px;
    font-family: var(--font-audrey);
    font-size: clamp(1.625rem, 1.018rem + 3.04vw, 3.75rem);
    font-weight: var(--regular-font);
    line-height: 150%;
    color: var(--black-color);
    text-transform: uppercase;

    span {
      color: var(--green-color);
    }
  }

  &__text {
    margin-bottom: 50px;
    font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
    line-height: 128%;
    color: var(--black-color);
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 100px;
    z-index: 1;
    width: 50%;
    min-height: 803px;
    background-image: url("../img/hero_img.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}

@media screen and (max-width:767px) {
  .hero {
    display: flex;
    align-items: center;

    &__info {
      max-width: 100%;
    }

    &::before {
      width: 80%;
      opacity: .4;
    }

  }
}


@media screen and (max-width:500px) {
  .hero {
    margin-top: 0;
    padding: 120px 0 50px;
    min-height: 100vh;

    &__headline,
    &__text {
      text-align: center;
    }

    &::before {
      width: 100%;
      min-height: 100vh;
      top: 0;
    }
  }
}
