.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  padding: 15px 0;
  width: 100%;
  background-color: var(--green-color);
  opacity: 1;
  transition: .2s ease-in;

  &__body {
    align-items: center;
    justify-content: space-between;
    gap: 5px;
  }

  &__logo {
    width: 120px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__menu {
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__list {
    align-items: center;
    gap: 50px;
  }

  &__link {
    font-family: var(--font-audrey);
    font-size: 18px;
    font-weight: var(--medium-font);
    line-height: 133%;
    color: var(--white-color);
    text-transform: uppercase;
    transition: all .2s ease-in;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #BB875E;
    }

    &:active {
      color: #C99B79;
    }
  }

  &__btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border: 1px solid var(--white-color);
    border-radius: 3px;
    padding: 20px 25px 15px;
    transition: .2s ease-in;

    span {
      position: relative;
      z-index: 2;
      font-family: var(--font-audrey);
      font-weight: var(--medium-font);
      line-height: 125%;
      text-transform: uppercase;
      color: var(--white-color);
      transition: .2s ease-in;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-color: var(--white-color);
      transform: translateY(-100%);
      transition: .2s ease-in;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: var(--green-color);
      }

      &::before {
        transform: translateY(0);
      }
    }

    &:active {
      span {
        color: var(--beige-color);
      }
    }
  }

}

.header--hidden {
  opacity: 0;
  pointer-events: none;
}



.header__link.focus-visible,
.header__logo.focus-visible,
.header__phone.focus-visible,
.header__burger.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}


.burger {
  position: relative;
  z-index: 1000;
  display: none;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  padding: 10px;
  background-color: var(--blue-color);
  cursor: pointer;

  &__line {
    position: absolute;
    right: 10px;
    top: 50%;
    width: 15px;
    height: 2px;
    border-radius: 10px;
    background-color: var(--white-color);
    transform: translateY(-50%);
    transition: opacity .2s ease-in-out;
  }

  &::before {
    content: "";
    position: absolute;
    right: 10px;
    top: 12px;
    border-radius: 10px;
    width: 18px;
    height: 2px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, top .2s ease-in-out;
  }

  &::after {
    content: "";
    position: absolute;
    right: 10px;
    bottom: 12px;
    border-radius: 10px;
    width: 10px;
    height: 2px;
    background-color: var(--white-color);
    transition: transform .2s ease-in-out, bottom .2s ease-in-out;
  }

  &--active {
    .burger__line {
      opacity: 0;
    }

    &::before {
      top: 50%;
      width: 20px;
      background-color: #fff;
      transform: rotate(45deg);
    }

    &::after {
      top: 50%;
      bottom: auto;
      width: 20px;
      background-color: #fff;
      transform: rotate(-45deg);
    }
  }
}


@media screen and (max-width:1024px) {
  .nav {
    position: fixed;
    right: 0%;
    top: 0;
    z-index: 100;
    flex-direction: column;
    justify-content: space-between;
    overflow: auto;
    padding: 100px 30px;
    width: 50vw;
    height: 100vh;
    background-color: var(--green-color);
    visibility: hidden;
    transform: translateX(100%);
    transition: transform .2s ease-in-out, visibility .2s ease-in-out;

    &--visible {
      visibility: visible;
      transform: translateX(0);
    }
  }

  .header {
    padding: 10px 0;

    &__body {
      display: flex;
      justify-content: space-between;
    }

    &__list {
      width: 100%;
      align-items: flex-start;
      flex-direction: column;
    }

    &__btn {
      display: none;
    }

    &__phone {
      display: flex;
    }
  }

  .burger {
    display: block;
  }
}

@media screen and (max-width:767px) {
  .nav {
    width: 80vw;
  }
}

@media screen and (max-width:500px) {
  .header {
    &__logo {
      width: 80px;
    }
  }

  .nav {
    width: 100vw;
  }
}
