.why-us {
  position: relative;
  overflow: hidden;
  background-color: #fff;

  &__container {
    position: relative;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__title {

  }

  &__slider {
    margin-top: 50px;
    padding: 40px 0;

    .swiper-slide-active {

      .why-us__images,
      .why-us__info {
        transform: scale(1);
      }
    }
  }

  &__slide {
    position: relative;
    align-items: center;
    justify-content: center;
    gap: 100px;
    transform: scale(0);
    transition: .2s ease-in;
  }

  &__info {
    border: 1.365px solid #BBA68A;
    border-radius: 3px;
    padding: 54px 40px 68px;
    max-width: 470px;
    transform: scale(0);
    transition: .2s ease-in;
  }

  &__subtitle {
    position: relative;
    top: -70px;
    max-width: max-content;
    margin: auto;
    padding: 0 5px;
    font-family: var(--font-audrey);
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    font-weight: var(--medium-font);
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: var(--green-color);
    background-color: var(--white-color);

  }

  &__text {
    font-size: clamp(1rem, 0.839rem + 0.8vw, 1.563rem);
    font-weight: 400;
    line-height: 128%;
    text-align: center;
    color: var(--grey-color);

    a {
      font-size: clamp(1rem, 0.839rem + 0.8vw, 1.563rem);
      font-weight: 400;
      line-height: 128%;
      text-align: center;
      color: var(--green-color);
    }
  }

  &__images {
    position: relative;
    width: 428px;
    height: 428px;
    transform: scale(0);
    text-align: center;
    transition: 1s ease-in-out;
  }

  &__image {
    position: absolute;
    left: 0;
    top: 0;
    border: 3.95px solid #E2E2E2;
    border-radius: 2.37px;
    width: 100%;
    height: 428px;
    object-fit: cover;
    transition: .2s ease-in;

    &--back {
      z-index: -1;
      top: -20px;
      left: -5px;
      transform: rotate(3.557deg);
    }
  }


  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 621px;
    height: 757px;
    background-image: url("../img/why-us-decor.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}


@media screen and (max-width:1024px) {
  .why-us {
    &__container {
      &::before {
        background-size: contain;
      }
    }
    &__slide {
      flex-direction: column;
      gap: 50px;
    }

    &__info {
      max-width: 100%;
    }
  }
}

@media screen and (max-width:600px) {
  .why-us {
    &__images {
      width: 90%;
    }
  }
}

@media screen and (max-width:500px) {
  .why-us {
    &__info {
      padding: 25px 20px 45px;
    }
    &__subtitle {
      top: -40px;
    }
  }
}
