.contact {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 690px;
    background-image: url("../img/contact-bg.jpg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    background-attachment: fixed;
  }

  &__body {
    position: relative;
    overflow: hidden;
    border-radius: 3px;
    margin: auto;
    padding: 70px;
    max-width: 959px;
    background-color: var(--green-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      background-image: url("../img/contact-decor.png");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__title {
    margin-bottom: 15px;
    font-family: var(--font-audrey);
    font-size: clamp(1.375rem, 0.964rem + 2.05vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    text-transform: uppercase;
    text-align: center;
    color: var(--white-color);
  }

  &__text {
    margin-bottom: 50px;
    font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
    line-height: 144%;
    text-align: center;
    color: var(--white-color);
  }
}

.form {
  position: relative;
  z-index: 3;
  margin: 0 auto;
  max-width: 590px;

  &__field {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 32px;
    }
  }

  &__label {
    position: absolute;
    left: 20px;
    top: -22px;
    display: inline-flex;
    align-items: center;
    padding: 5px 15px;
    border-radius: 3px;
    width: max-content;
    font-size: 18px;
    line-height: 144%;
    color: var(--white-color);
    background-color: var(--green-color);
  }

  &__input {
    display: block;
    outline: none;
    padding: 26px 20px 18px 20px;
    border: 1px solid #E2E2E2;
    border-radius: 3px;
    font-size: 18px;
    line-height: 144%;
    color: var(--white-color);
    background-color: var(--green-color);
    transition: .2s ease-in;

    &::placeholder {
      font-size: 18px;
      line-height: 144%;
      color: #9DB8B5;
      transition: opacity .2s ease-in;
    }

    &:focus {
      &::placeholder {
        opacity: 0;
      }
    }
  }

  &__textarea {
    margin-bottom: 30px;
    resize: none;
    min-height: 155px;
  }

  &__btn {
    position: relative;
    display: block;

    span {
      position: relative;
      z-index: 2;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 3px;
      width: 350px;
      padding: 23px 25px 17px;
      font-family: var(--font-audrey);
      font-size: 18px;
      font-weight: var(--medium-font);
      line-height: 133%;
      text-transform: uppercase;
      color: var(--green-color);
      background-color: var(--white-color);
      transform: translate(20px, 20px);
      transition: .2s ease-in;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      border: 1px solid var(--white-color);
      border-radius: 3px;
      width: 350px;
      height: 70px;
      transition: .2s ease-in;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        transform: translate(0, 0);
      }

      &::before {
        opacity: 0;
      }
    }

    &:active {
      span {
        color: var(--beige-color);
      }
    }
  }
}

.form__input.focus-visible {
  outline: 1px solid rgb(246, 3, 3);
  outline-offset: 3px;
}

@media screen and (max-width:1024px) {
  .contact {
    padding: 0 0 100px;
  }
}


@media screen and (max-width:767px) {
  .contact {
    &__body {
      padding: 70px 10px;
    }
  }

  .form {
    &__btn {
      span {
        width: 70vw;
      }

      &::before {
        width: 100%;
      }
    }
  }
}
