.contact-address {
  padding: 100px 0 0;
  background-color: var(--green-color);

  &__container {
    justify-content: space-between;
    gap: 30px;
    border-bottom: 1px solid rgba(157, 184, 181, 0.20);
    padding-bottom: 50px;
  }

  &__title {
    margin-bottom: 50px;
    font-family: var(--font-audrey);
    font-size: clamp(1.375rem, 0.964rem + 2.05vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    text-transform: uppercase;
    color: var(--white-color);
  }

  &__map {
    width: 565px;
    max-width: 100%;
    height: 301px;

    iframe {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 3px;
    }
  }
}

.address {
  display: block;
  width: 635px;
  max-width: 100%;
  font-style: normal;

  &__list {
    justify-content: space-between;
    gap: 15px;

    &-item {
      align-items: flex-start;
      gap: 15px;
      max-width: 193px;
      color: var(--white-color);
    }

    &-icon {
      width: 30px;
      height: 30px;

      svg {
        display: block;
        width: 100%;
        height: 100%;
        stroke: var(--white-color);
        fill: transparent;
      }
    }

    &-title {
      margin-bottom: 10px;
      font-size: 18px;
      font-weight: var(--semibold-font);
      line-height: 144%;
    }

    &-text {
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      line-height: 144%;
    }

    &-link {
      color: var(--white-color);
      transition: .2s ease-in;
      &:focus {
        outline: none;
      }

      @include hover {
        color: #BB875E;
      }

      &:active {
        color: #C99B79;
      }
    }
  }
}

@media screen and (max-width:767px) {
  .contact-address {
    &__container {
      flex-direction: column;
    }
    &__map {
      width: 100%;
    }
  }
}

@media screen and (max-width:500px) {
  .address {
    &__list {
      flex-direction: column;
      &-item {
        width: 100%;
        max-width: 100%;
      }
    }
  }
}
