.services {
  position: relative;
  overflow: hidden;
  background-color: var(--green-color);

  &__container {
    position: relative;
    padding-top: 15px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      width: 100%;
      height: 20px;
      background-image: url("../img/services-decor.png");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__headline {
    text-align: center;
    color: var(--white-color);
  }

  &__title {
    margin-top: 35px;
    color: #E2E2E2;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    &-item {
      cursor: pointer;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 30px;
      border-radius: 3px;
      min-height: 410px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      &:nth-child(1) {
        background-image: url("../img/services1.jpg");
      }

      &:nth-child(2) {
        background-image: url("../img/services2.jpg");
      }

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        border-radius: 3px;
        width: 100%;
        height: 100%;
        background: rgba(43, 42, 41, 0.30);
        backdrop-filter: blur(5px);
        transition: .2s ease-in;
      }

      &:focus {
        outline: none;
      }

      @include hover {
        &::before {
          opacity: 0;
        }
      }

      span {
        position: relative;
        z-index: 2;
        font-family: var(--font-audrey);
        font-size: 25px;
        font-weight: var(--medium-font);
        line-height: 120%;
        text-transform: uppercase;
        text-align: center;
        color: var(--white-color);
      }
    }
  }

  &::before {
    content: "";
    position: absolute;
    right: 0px;
    top: -100px;
    z-index: 1;
    width: 500px;
    height: 478px;
    background-image: url("../img/decor2.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}

@media screen and (max-width:1024px) {
  .services {
    &__container {
      &::before {
        background-size: contain;
      }
    }
  }
}

@media screen and (max-width:767px) {
  .services {
    &__list {
      grid-template-columns: 1fr;
    }
  }
}
