.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: none;
}

.thank-you-popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  padding: 50px 50px 70px;
  width: 80%;
  min-height: 476px;
  max-width: 627px;
  border-radius: 3px;
  overflow: hidden;
  transform: scale(0.7);
  background-color: var(--green-color);
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease;

  &::before {
    content: "";
    position: absolute;
    left: -100px;
    bottom: 0;
    width: 428px;
    height: 519px;
    background-image: url("../img/decor-6.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}

.thank-you-popup-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 350px;
}

.thank-you-popup .close {
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  font-size: 24px;
  color: #333;
  color: #fff;
  opacity: .6;
  transition: .2s ease-in;

  &:hover {
    opacity: 1;
    text-decoration: none;
    cursor: pointer;
  }
}

@media screen and (max-width:500px) {
  .thank-you-popup {
    margin: 10% auto;
    width: 90%;
    padding: 50px 20px 70px;

    .form__btn span {
      width: 100%;
    }

  }
}
