.features {
  position: relative;

  &__container {
    gap: 70px;
  }

  &__images {
    padding-top: 60px;
  }

  &__image {
    &--lg {
      position: relative;
      z-index: 2;
      margin-bottom: 30px;
      max-width: 558px;
      height: 481px;
    }

    &--sm {
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 2;
      max-width: 549px;
      height: 252px;
    }
  }

  &__info {
    padding-top: 50px;
    max-width: 667px;
  }

  &__title {
    margin-bottom: 30px;
    font-family: var(--font-audrey);
    font-size: clamp(1.5rem, 1.125rem + 1.88vw, 2.813rem);
    font-weight: var(--regular-font);
    line-height: 140%;
    color: var(--green-color);
    text-transform: uppercase;
    letter-spacing: 1px;

    span {
      white-space: normal;
      color: var(--black-color);
    }
  }

  &__text {
    margin-bottom: 30px;
    font-size: clamp(1.125rem, 1rem + 0.63vw, 1.563rem);
    line-height: 128%;
    color: var(--grey-color);
  }

  &__list {
    margin-bottom: 30px;

    &-item {
      position: relative;
      padding-left: 45px;
      font-size: clamp(1rem, 0.964rem + 0.18vw, 1.125rem);
      line-height: 144%;
      color: var(--light-gray);

      strong {
        font-weight: var(--semibold-font);
        color: var(--grey-color);
      }

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 25px;
        height: 25px;
        background-image: url("../img/list-star.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        transform: translateY(-50%);
      }

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__btn {
    display: flex;
    justify-content: flex-end;
    &::before {
      left: auto;
      right: 0;
      top: 0;
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 100px;
    z-index: 1;
    border: 1px solid #ECE1D6;
    width: 571px;
    height: 734px;
  }
}

@media screen and (max-width:1250px) {
  .features {
    &__image {
      &--sm {
        position: relative;
      }
    }
    &__info {
      position: relative;
      z-index: 2;
    }
    &__btn {
      justify-content: flex-start;
      &::before {
        left: 0;
      }
    }
  }
}

@media screen and (max-width:1024px) {
  .features {
    &__container {
      gap: 30px;
    }
  }
}

@media screen and (max-width:900px) {
  .features {
    &__container {
      flex-direction: column;
    }
    &__info {
      max-width: 100%;
    }
    &__image {
      &--lg,
      &--sm {
        max-width: 100%;
      }
    }
  }
}

@media screen and (max-width:500px) {
  .features {
    &__title {
      margin-bottom: 15px;
    }
    &__text {
      margin-bottom: 15px;
    }
    &__image {
      &--lg {
        height: 400px;
      }
      &--sm {
        height: 150px;
      }
    }
  }
}
